
.vimeography-thumbnail {
  margin: 0;
  width: 100%;
}

/* IE11 Support */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .vimeography-thumbnail {
    flex: 0 0 25%;
  }
}

.vimeography-link {
  display: block;
  margin: 0 0 20px;
}

.vimeography-link-active {
}

.vimeography-thumbnail-img {
  opacity: 0.9;
  transition: all 250ms ease-in-out;

  width: 100%;
  padding: 0;
  box-shadow: #000 0em 0em 0em; /* Fixes weird firefox rendering with opacity changes */
  cursor: pointer;

  &:hover {
    opacity: 1;
  }
}

figcaption {
  text-align: center;
  position: relative;
  overflow: hidden;
}

.vimeography-title {
  color: #222;
  font-family: sans-serif;
  font-size: 11px;
  line-height: 1.2em;
  overflow: hidden;
  font-weight: 500;
  margin: 0 0 8px;
  display: block;
}

.vimeography-description {
  color: #aaa;
  font-family: serif;
  font-size: 11px;
  line-height: 1.2em;
  font-weight: 300;
  text-transform: lowercase;
  padding: 0;
  margin: 0;

  display: block;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
